        methods: {
            insert_jianbaosucai() {
                this.piliang_jiarujianbaosucai = true
                let num = 0
                if (this.checked === true) {
                    num = this.info_list.length
                } else {
                    for (let index in this.info_list) {
                        if (this.info_list[index].is_checked === true) {
                            num++
                        }
                    }
                }
                this.news_number = num
            },
            insert_shoucangjia() {
                this.piliang_jiarushoucangjia = true
                let num = 0
                if (this.checked === true) {
                    num = this.info_list.length
                } else {
                    for (let index in this.info_list) {
                        if (this.info_list[index].is_checked === true) {
                            num++
                        }
                    }
                }
                this.news_number = num
            },
            zidingyishijian() {
                if (this.radio1 === 7) {
                    this.qitashijian = true
                    this.kongbai = false
                } else {
                    this.qitashijian = false
                    this.kongbai = true
                }
            },
            return_Infolist() {
                this.activeName = 'first'
            },
            search_source(selected) {
                if (selected.length === 0) {
                    this.checkList.push('0')
                } else if (selected[selected.length - 1] === '0') {
                    this.checkList = ['0']
                } else {
                    let index = this.checkList.indexOf('0')
                    if (index > -1) this.checkList.splice(index, 1)
                }
            },
            xinyuanquyu() {
                if (this.value !== '') {
                    this.radio5 = 1
                }
            },
            xinyuanquyu_danxuan() {
                if (this.radio5 === 2 | 3) {
                    this.value = ''
                }
            },
            chooseAll() {
                for (let index in this.info_list) {
                    this.info_list[index].is_checked = this.checked
                }
            },

            goto_dingxiangjiance() {
                this.activeName = 'fifth'
            },

            markAllRead() {
                for (let index in this.info_list) {
                    if (this.info_list[index].is_checked) {
                        this.info_list[index].is_read = true
                    }
                }
            },

            change_readState(id) {
                axios.post('/api/info/readstate_change', {
                    'id': id
                }).then(r => {
                    console.log('response %o', r)
                    if (r.data.code === 0) {
                        // this.info_list = r.data.info_list
                        let info = this.info_list.find(e => e.id === id)
                        let index = this.info_list.indexOf(info)
                        this.info_list[index]['is_read'] = !this.info_list[index]['is_read']
                    }
                }).catch(err => {
                    console.log('error %o', err)
                })
            },
            changeSensitive(id) {
                axios.post('/api/info/sensitive_change', {
                    'id': id
                }).then(r => {
                    console.log('response %o', r)
                    if (r.data.code === 0) {
                        // this.info_list = r.data.info_list
                        let info = this.info_list.find(e => e.id === id)
                        let index = this.info_list.indexOf(info)

                        this.info_list[index]['sensitive_state'] = !this.info_list[index]['sensitive_state']
                    }
                }).catch(err => {
                    console.log('error %o', err)
                })
            },
            chooseInfo(id) {
                for (let index in this.info_list) {
