            changeForm() {
                if (this.type === '高级') {
                    this.showform1 = false
                    this.showform2 = true
                    this.type = '快速'
                    this.form_title = '高级'
                }
                else {
                    this.showform1 = true
                    this.showform2 = false
                    this.type = '高级'
                    this.form_title = '快速'
                }
            },
            handleClick(tab, event) {
                this.initGraph()
                if (tab._props['name'] === 'collapse') {
                    this.mainContentSpan = 24
                    this.asideSpan = 0
                } else {
                    this.mainContentSpan = 20
                    this.asideSpan = 4
                }
                this.$nextTick(function () {
                    this.myChart1.resize()
                    this.myChart2.resize()
                    for (let i in this.$refs) {
                        if (this.$refs[i].hasOwnProperty('echarts')) {
                            this.$refs[i].echarts.resize()
                        }
                    }
                })
            },
            refresh() {
                this.initGraph()
            },
            initGraph() {
                this.infochartData.rows = []
                this.attchartData.rows = []
                this.attributechartData.rows = []

                let now = (new Date()).getTime()
                for (let i = 0; i < 6; i++) {
                    now += 21600000
                    let timeStr = formatTime(new Date(now))

                    let wb = Math.ceil(Math.random() * 21 + 0)
                    let wz = Math.ceil(Math.random() * 3 + 0)
                    let wx = Math.ceil(Math.random() * 2 + 0)
                    let lt = Math.ceil(Math.random() * 2 + 0)
                    let khd = Math.floor(Math.random() * 2 + 0)
                    let xw = Math.floor(Math.random() * 2 + 0)
                    let sum = wb + wz + wx + lt + khd + xw
                    this.infochartData.rows.push({
                        'time': timeStr,
                        '微博': wb,
                        '网站': wz,
                        '微信': wx,
                        '论坛': lt,
                        '客户端': khd,
                        '新闻': xw,
                        '全部': sum
                    })
                }

                let now2 = (new Date()).getTime()
                for (let i = 0; i < 25; i++) {
                    now2 += 21600000
                    let timeStr = formatTime(new Date(now2))

                    let wb = Math.ceil(Math.random() * 61 + 50)
                    let wx = Math.ceil(Math.random() * 21 + 10)
                    let wz = Math.ceil(Math.random() * 5 + 1)
                    let lt = Math.ceil(Math.random() * 4 + 0)
                    let khd = Math.ceil(Math.random() * 5 + 1)
                    let sp = Math.ceil(Math.random() * 4 + 0)
                    let xw = Math.ceil(Math.random() * 5 + 1)
                    let zw = Math.ceil(Math.random() * 4 + 0)
                    let bk = Math.ceil(Math.random() * 5 + 1)
                    let bke = Math.ceil(Math.random() * 4 + 0)
                    let sum = wb + wx + wz + lt + khd + sp + xw + zw + bk + bke
                    this.attchartData.rows.push({
                        'time': timeStr,
                        '微博': wb,
                        '微信': wx,
                        '网站': wz,
                        '论坛': lt,
                        '客户端': khd,
                        '视频': sp,
                        '政务': zw,
                        '新闻': xw,
                        '报刊': bk,
                        '博客': bke,
                        '全部': sum
                    })
                }
                let now3 = (new Date()).getTime()
                for (let i = 0; i < 20; i++) {
                    now3 += 21600000
                    let timeStr = formatTime(new Date(now3))

                    this.attributechartData.rows.push({
                        'time': timeStr,
                        '敏感': Math.ceil(Math.random() * 11 + 2),
                        '非敏感': Math.ceil(Math.random() * 31 + 120),

                    })
                }
            },

